/* global chrome */
const config = require('config');
const $ = require('jquery');

module.exports = {
  save: function (connection) {
    connection.timestamp = new Date().getTime();
    // save connection details to local storage for cacheing purposes
    chrome.storage.local.set({connection: connection});
  },
  
  get: function (next) {
    const self = this;
    chrome.storage.local.get('connection', (result) => {
      result = result || {};
      const connection = result.connection || {};
      // if the timestamp is greater than or equal to 1 week ago, don't fetch new connection info.
      // use the cached info.
      const expiration = new Date().getTime() - config.api.connection.maxAge;
      
      if (typeof connection.timestamp !== 'undefined' &&
              connection.timestamp >= expiration) {
        if (typeof next === 'function') {
          next(connection);
        }
        return;
      }
                
      $.ajax({
        type: 'GET',
        url: config.api.connection.path,
        success: (response) => {
          self.save(response.connection);
          if (typeof next === 'function') {
            next(response.connection);
          }
        },
        error: (e) => {
          console.error('connection error', e);
          if (typeof next === 'function') {
            next(null);
          }
        }
        
      });
    
    });
  },
  
  refresh: function (connection) {
    delete connection.timestamp;
    // save connection details with undefined timestamp
    chrome.storage.local.set({connection: connection});
    this.get();
  }
};


