/* global chrome, document */
const webspeedStorage = require('storage/webspeed');
const gtm = require('gtm');

module.exports = {
  externalLink (href) {
    chrome.tabs.create({ url: href });
    gtm.push({'event': 'externalClick', 'externalClick': href});
  },

  saveWebspeed() {
    let webspeed = document.getElementById('webspeed').checked;
    if (webspeed) {
      // clear webspeed local storage, if clicked and it's enabled.
      webspeedStorage.disable();
      return;
    }
    webspeedStorage.enable();
  },

  setWebspeedOption() {

    // Use default value color = 'red' and likesColor = true.
    chrome.storage.local.get('options', function(res) {

      if (res.options && res.options.webspeed) {
        document.getElementById('webspeed').checked = res.options.webspeed;
      }
    });
  },

  parseUrl: function (url) {
    let host;
    // Only if www. is at the beginning of url do we remove www. 
    // Otherwise send the whole url. Makes sure we get subdomains and funny wwww. edge cases
    (url.host.indexOf("www.") === 0) ? host = encodeURI(url.host.replace('www.', '')) : host = encodeURI(url.host);

    const pathname = encodeURI(url.pathname);

    return {host: host, pathname: pathname};
  }
};



