/* global chrome, window */
const config = require('config');
const $ = require('jquery');
const md5 = require('md5');
const connectionStorage = require('storage/connection');
const gtm = require('gtm');
const helpers = require('client/helpers');
const _ = require('lodash');

module.exports = {
  sortKeys: function(obj, id) {
    let keys = [];
    // create array of keys.
    for (let key in obj) {
      keys.push(key);
    }

    // sort keys by id
    keys.sort(function(a, b) {
      return obj[a][id] - obj[b][id];
    });
    return keys;
  },

  disable: function () {
    chrome.storage.local.remove('webspeed');
    gtm.push({'event': 'webspeedDisabled', 'webspeed': 'disabled'});
    chrome.storage.local.set({
      options: {
        webspeed: false
      }
    });
  },

  enable: function (type) {
    if (type !== 'install') {
      gtm.push({'event': 'webspeedEnabled', 'webspeed': 'enabled'});
    }
    chrome.storage.local.set({
      options: {
        webspeed: true
      }
    });
  },

  pruneHost: function (host) {
    chrome.storage.local.get('webspeed', (page) => {
      const length = Object.keys(page.webspeed[host]).length;
      if (length <= config.webspeed.hostMax) {
        return;
      }

      // sort host keys by timestamp
      const sortedKeys = this.sortKeys(page.webspeed[host], 'timestamp');

      for (let key in sortedKeys) {
        // first item is the oldest, delete it.
        delete page.webspeed[host][sortedKeys[key]];
        break;
      }

      // save the object to storage.
      chrome.storage.local.set(page);
    });
  },

  pruneWebSpeedStorage: function() {
    chrome.storage.local.get('webspeed', (page) => {

      const maxBytesAllowed = config.webspeed.maxStorageBytes *
        (config.webspeed.prunePercentage / 100);

      let sortedKeys;

      let key = 0;

      // setup function to call async getBytesInUse until maxBytes are met.
      const pruneList = () => {
        chrome.storage.local.getBytesInUse(null, (bytes) => {
          // if the current bytes are still greater than the bytes needing pruned,
          // keep deleting records
          if (!sortedKeys) {
            sortedKeys = this.sortKeys(page.webspeed, 'timeStamp');
          }

          if (bytes <= maxBytesAllowed || typeof page.webspeed[sortedKeys[key]] === 'undefined') {
            return;
          }
          // delete by count, the first records being the oldest.
          delete page.webspeed[sortedKeys[key]];
          key ++;
          chrome.storage.local.set({'webspeed': page.webspeed});

          // call function again.
          pruneList();
        });
      };
      pruneList();
    });
  },

  saveWebspeed: function (webspeed) {
    if (config.api.webspeeds && config.api.webspeeds.path) {

      // retrieve the connection info prior to posting webspeed information.
      connectionStorage.get(function (connection) {
        if (connection) {
          _.merge(webspeed, {connection: connection});
        }
        $.ajax({
          contentType: 'application/json',
          type: 'POST',
          url: config.api.webspeeds.path,
          data: JSON.stringify(webspeed),
          headers: {
            'X-Cache-Id': md5(JSON.stringify(webspeed) + '3e75a18b95')
          },
          success: function(data, test, request) {
            var cacheId = request.getResponseHeader('X-Cache-Id');

            chrome.storage.local.get('cacheId', (globals) => {

              if (Object.keys(globals).length === 0 ) {
                globals = {};
                globals.cacheId = null;
              }

              // if the id's match, go no further
              if (globals.cacheId === cacheId) {
                return;
              }

              const minAge = connection.timestamp + config.api.connection.minAge;
              var now = new Date().getTime();

              // if the minimum time buffer hasn't been reached, go no further.
              if (now <= minAge) {
                return;
              }

              globals.cacheId = cacheId;
              // save simple webspeed to local storage.
              chrome.storage.local.set(globals);

              // refresh connection information.
              connectionStorage.refresh(connection);
            });
          }
        });
      });

    }
  },

  save: function (webspeed) {
    const url = helpers.parseUrl(window.location);

    chrome.storage.local.get('webspeed', (page) => {
      var now = new Date().getTime();

      if (Object.keys(page).length === 0 ) {
        page = {};
        page.webspeed = {};
      }
      page.webspeed[url.host] = page.webspeed[url.host] || {};

      // set timestamp on host
      page.webspeed[url.host].timeStamp = now;

      // set page url information.
      page.webspeed[url.host][url.pathname] = {
        domCompleteDuration: webspeed.domCompleteDuration,
        timestamp: now
      };

      // save webspeed data to db
      this.saveWebspeed(webspeed);

      // save simple webspeed to local storage.
      chrome.storage.local.set(page);

      // maintain maximum urls saved under hostname.
      this.pruneHost(url.host);
      // maintain maximum storage size.
      this.pruneWebSpeedStorage();
      // send message that the UI can now display the webspeed
      chrome.runtime.sendMessage({message: 'displayWebSpeed'});
    });
  }
};



