/* global chrome, navigator */

var engine = require('speedtest-core/engine-api');
var Backbone = require('backbone');
var $ = require('jquery');
var config = require('config');
var gtm = require('gtm');

Backbone.$ = $;

module.exports = {
  engine: engine,

  speedtest: {
    started: false,
    goClicked: false,
    hasBeenRunOnce: false,
    sortedServers: [],
    onlineInterval: '',
    reset: false,
    engineState: 'UNINITIALIZED'
  },

  initialize: function () {
    if (this.speedtest.started) {
      this.testRunning();
      return;
    }
    // initialize the engine if it's not already initialized.
    if (this.speedtest.engineState === 'UNINITIALIZED') {
      this.engine.initialize({
        engine: 'js',
        automaticTest: false,
        jsEngine: config.jsEngine,
        callbacks: {
          updateProgress: this.updateProgress.bind(this),
          serversSorted: this.serversSorted.bind(this),
          engineAvailable: this.engineAvailable.bind(this),
          updateLatencyResult: this.updateLatencyResult.bind(this),
          updateDownloadResult: this.updateDownloadResult.bind(this),
          updateUploadResult: this.updateUploadResult.bind(this),
          setFinished: this.setFinished.bind(this),
          error: this.errorMessage.bind(this),
          resultSaved: this.resultSaved.bind(this)
        }
      });
      this.engine.triggerEngine();
      this.speedtest.engineState = 'INITIALIZED';
    } else {
      // since already initialized, the engine is available, let the UI know.
      this.engineAvailable();
    }

    // check for previous test results
    chrome.storage.local.get(['latencyResult', 'downloadResult', 'uploadResult', 'resultId'], (data) => {
      if (data.downloadResult &&
          data.uploadResult &&
          data.latencyResult &&
          !this.speedtest.reset &&
          this.speedtest.hasBeenRunOnce) {
          this.resultSaved(data.resultId);
        this.showPreviousResults(data.latencyResult, data.downloadResult, data.uploadResult);
      }
    });

    this.getServers((servers) => {
      servers = servers.slice(0, config.api.servers.limit);
      this.engine.sortServers(servers);
    });

    this.resetIcon();

    this.setTestStage('init');
  },

  resetIcon: function () {
    this.setIcon('gauge');
    this.setBadge('', '#9193a8');
  },

  updateIcon: function (text, stage) {
    var color = '#9193a8';
    var type = stage;
    // ping is default color, so not included here
    if (stage === 'download') {
      color = '#1fa4e9';
    } else if (stage === 'upload') {
      color = '#a244da';
    } else if (stage === 'error') {
      color = '#ff3366';
      type = 'gauge';
    } else if (stage === 'finished') {
      color = '#2de5d1';
      type = 'gauge';
    } else {
      type = 'gauge';
    }
    // if views length is > 1, the popup is open
    if (chrome.extension.getViews().length > 1) {
      this.resetIcon();
    } else {
      this.setIcon(type);
      this.setBadge(text, color);
    }
  },

  setBadge: function (text, color) {
    text = text || '';
    chrome.browserAction.setBadgeText({ text: text });
    if (color) {
      chrome.browserAction.setBadgeBackgroundColor({ color: color });
    }
  },

  setIcon: function (type) {
    if (type) {
      chrome.browserAction.setIcon({
        path: {
          19: `images/${type}-19.png`,
          38: `images/${type}-38.png`
        }
      });
    }
  },

  SpeedToMbps: function (speed) {
    return speed !== null ? (speed / 125000).toFixed(2) : null;
  },

  SpeedToKbps: function (speed) {
    return speed !== null ? (speed * 8 / 1000).toFixed(0) : null;
  },

  monitorOnlineStatus: function (monitor) {
    clearInterval(this.speedtest.onlineInterval);
    if (!monitor) {
        return;
    }

    // check online status every second.
    this.speedtest.onlineInterval = setInterval(() => {
      (navigator.onLine) ? config.checkInternet.counter = 0 : config.checkInternet.counter += 1;
      if (config.checkInternet.counter === 10) {
        this.errorMessage('no internet',  'Internet Connection Lost','internet_lost');
        clearInterval(this.speedtest.onlineInterval);
        config.checkInternet.counter = 0;
      }
    }, config.checkInternet.interval);

  },

  engineAvailable: function () {
    chrome.runtime.sendMessage({message: 'engineAvailable'});
  },

  serversSorted: function (servers) {
    this.speedtest.sortedServers = servers;
    chrome.storage.local.set({sortedServers: servers});
    chrome.runtime.sendMessage({message: 'serversSorted'});
  },

  updateProgress: function (test, result, percentage) {
    const mbps = parseInt(this.SpeedToMbps(result), 10).toString();
    this.updateIcon(mbps, test);
    chrome.runtime.sendMessage({
      message: 'updateProgress',
      test: test,
      result: result,
      percentage: percentage
    });
  },

  updateLatencyResult: function (result) {
    if (this.speedtest.engineState === 'START_REQUESTED' && !this.speedtest.started) {
      if (this.speedtest.reset) {
        return;
      }
      if (result !== null) {

        this.speedtest.started = true;
        chrome.storage.local.set({latencyResult: result});

        chrome.runtime.sendMessage({message: 'latencyResult', result: result});

        gtm.push({'event': 'startTest'});
        gtm.push({'ping': result});

        var popup = chrome.extension.getViews({ type: 'popup' });
         // only push these events if the popup is closed
        if (popup.length === 0) {
          var manifest = chrome.runtime.getManifest();
          gtm.push({'extensionVersion': manifest.version});

          chrome.storage.local.get('options', function(res) {
            if (res.options) {
              var status = res.options.webspeed ? 'enabled' : 'disabled';
              gtm.push({'webspeed': status});
            } else {
              gtm.push({'webspeed': 'disabled'});
            }
          });
        }

        // give ping animation time to complete
        setTimeout(() => {
          this.setTestStage('download');
          this.engine.nextStep('download');
        }, 1000);
      }
    }
  },

  updateDownloadResult: function (result) {
    chrome.storage.local.set({downloadResult: result});
    if (this.speedtest.reset) {
      return;
    }
    chrome.runtime.sendMessage({message: 'downloadResult', result: result});
    if (result !== null && result !== 0) {
      this.setTestStage('upload');
      this.engine.nextStep('upload');
    }
  },

  updateUploadResult: function (result) {
    chrome.storage.local.set({uploadResult: result});
    if (this.speedtest.reset) {
      return;
    }
    chrome.runtime.sendMessage({message: 'uploadResult', result: result});

    this.setTestStage('finished');
  },

  showPreviousResults: function (latency, download, upload) {
    chrome.runtime.sendMessage({
      message: 'showPreviousResults',
      latency: latency,
      download: download,
      upload: upload
    });
  },

  setFinished: function () {
    this.updateIcon(' ', 'finished');
    chrome.runtime.sendMessage({message: 'setFinished', reset: this.speedtest.reset});

    gtm.push({'event': 'completeTest'});

    this.monitorOnlineStatus(false);
    this.speedtest.started = false;
    this.speedtest.engineState = 'INITIALIZED';
    this.speedtest.hasBeenRunOnce = true;
  },

  resultSaved: function (resultId) {
    if (resultId) {
      chrome.runtime.sendMessage({message: 'resultSaved', resultId: resultId});
    }
    chrome.storage.local.set({resultId: resultId});

    chrome.storage.local.get(['uploadResult', 'downloadResult', 'latencyResult'], (data) => {
      gtm.push({
        'resultId': resultId,
        'download': this.SpeedToKbps(data.downloadResult),
        'upload': this.SpeedToKbps(data.uploadResult),
        'latency': data.latencyResult,
        'event': 'resultSaved'
      });
    });
  },

  errorMessage: function (internal, message, id) {
    // trigger error
    message = message || 'Could not connect';
    id = id || 'could_not_connect';
    internal = internal || 'no message set';

    this.updateIcon(' ', 'error');
    chrome.runtime.sendMessage({message: 'errorMessage', error: {'message': message, 'id': id}});
    gtm.push({'event': 'error', 'error_message': message, 'internal_message': internal});

    this.resetTest();
  },

  testRunning: function () {
    chrome.runtime.sendMessage({message: 'testRunning'});
  },

  setTestStage: function (stage) {
    this.updateIcon('--', stage);
    chrome.runtime.sendMessage({message: 'setTestStage', stage: stage});
  },

  // get servers
  getServers: function (callback) {
    const servers =  new Backbone.Collection;
    servers.url =  config.api.servers.path + config.api.servers.limit;
    servers.fetch({
      dataType: 'jsonp',
      success: function (results) {
        callback(results);
      }
    });
  },

  startTest: function (server) {
    this.speedtest.engineState = 'START_REQUESTED';
    this.speedtest.reset = false;
    // reset results
    this.updateLatencyResult(null);
    this.updateDownloadResult(null);
    this.updateUploadResult(null);
    this.engine.startTest(server);
    this.setTestStage('ping');
    this.monitorOnlineStatus(true);
  },
  shareResult: function(share) {
    chrome.runtime.sendMessage({message: 'shareResult', share: share});
  },

  resetTest: function () {
    this.resetIcon();
    this.speedtest.reset = true;
    this.engine.stopEngine();
    this.monitorOnlineStatus(false);

    // reset result numbers
    this.updateLatencyResult(null);
    this.updateDownloadResult(null);
    this.updateUploadResult(null);

    // tell UI the test is finished/ stop engine and re-initialize
    chrome.runtime.sendMessage({message: 'setFinished', reset: this.speedtest.reset});
    this.resultSaved(null);
    this.monitorOnlineStatus(false);
    this.speedtest.started = false;
    this.speedtest.engineState = 'INITIALIZED';
  }
};



