/* global chrome document window */
var engineBridge = require('backgroundEngineBridge');
var Backbone = require('backbone');
var $ = require('jquery');
const webspeedStorage = require('storage/webspeed');

var gtm = require('gtm');

$(document).ready(function() {
  // instantiate GTM
  gtm.instantiate();
});

// put engine in global scope for debugging reasons.
window.EXT = window.EXT || {};
window.EXT.engineBridge = engineBridge;
window.EXT.engine = engineBridge.engine;

Backbone.$ = $;

chrome.runtime.sendMessage({message: 'backgroundLoaded'});
chrome.runtime.onInstalled.addListener(function() {
  chrome.storage.local.get('options', (res) => {

    // Only enable webspeed on install if new install or user had box checked before update.
    if (typeof res.options === "undefined" || res.options.webspeed) {
      webspeedStorage.enable('install');
    }
  });
});

chrome.webRequest.onHeadersReceived.addListener(function(n) {
  chrome.storage.local.set({'httpResponse': n.statusCode});
}, {
    urls: ["<all_urls>"],
    types: ["main_frame"]
}, ["responseHeaders"]);


function startSpeedtest() {

  if (engineBridge.speedtest.started) {
    return;
  }

  // if popup is open trigger the button click.
  const popup = chrome.extension.getViews({ type: 'popup' });
  if (popup.length > 0) {
    chrome.runtime.sendMessage({message: 'beginTestClicked'});
    return;
  }

  engineBridge.initialize();

  // setup listeners
  engineBridge.engine.listen('serversSorted', function (serversSorted) {
    engineBridge.startTest(serversSorted[0]);
    engineBridge.engine.stopListening('serversSorted');
  });

  engineBridge.engine.listen('finished', function () {
    engineBridge.speedtest.goClicked = false;
    engineBridge.engine.stopListening('finished');
  });

  engineBridge.engine.listen('error', function () {
    engineBridge.resetTest();
    engineBridge.engine.stopListening('error');
  });

}

function initialize (request) {
  if (request.message === 'initialize') {
    engineBridge.initialize();
  }
}
chrome.runtime.onMessage.addListener(initialize);

function startTest (request) {
  if (request.message === 'startTest') {
    if (engineBridge.speedtest.started) {
      return;
    }

    if (!request.server && !request.server.attributes) {
      console.error('no server defined', request);
      return;
    }

    var server = new Backbone.Model(request.server.attributes);
    engineBridge.startTest(server);

  }
}
chrome.runtime.onMessage.addListener(startTest);

function isTestStarted (request) {
  if (request.message === 'isTestStarted') {
    const sortedServers = engineBridge.speedtest.sortedServers[0];
    if (!sortedServers) {
      return;
    }
    chrome.runtime.sendMessage({
      message: 'testStarted',
      status: engineBridge.speedtest.started,
      selectedServer: {
        attributes: engineBridge.speedtest.sortedServers[0].attributes
      }
    });
  }
}
chrome.runtime.onMessage.addListener(isTestStarted);

function shareResult (request) {
  if (request.message === "shareResult") {
    engineBridge.shareResult(request.share);
  }
}
chrome.runtime.onMessage.addListener(shareResult);

function resetTest (request) {
  if (request.message === 'resetTest') {
    engineBridge.resetTest();
  }
}
chrome.runtime.onMessage.addListener(resetTest);

var currentIPList	= {};

chrome.webRequest.onCompleted.addListener(
  function(info) {
    if (currentIPList[info.initiator] !== undefined) return;
    return currentIPList[info.initiator] = info.ip;
  },
  {
  urls: [],
  types: []
  },
  []
);

chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
  if (request.greeting === 'get_ip') {
    sendResponse(currentIPList[request.t]);
  }
});


